/*
 * BaseStore
 * @flow
 */

var AppDispatcher = require('AppDispatcher');
var EventEmitter = require('fbemitter').EventEmitter;
var React = require('react');

var invariant = require('invariant');

class BaseStore extends EventEmitter {

    constructor() {
        super();
        this.actions = {};
        this.dispatchToken = AppDispatcher.register(this.onDispatch.bind(this));
    }

    onDispatch(payload: Object): bool {require('flowcheck/assert').check(arguments, require('flowcheck/assert').arguments([require('flowcheck/assert').object])); var ret = (function (payload) {
        var callback = this.actions[payload.type];
        callback && callback(payload);
        return true;
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').boolean);}

    addAction(action: string, callback: Function): void {require('flowcheck/assert').check(arguments, require('flowcheck/assert').arguments([require('flowcheck/assert').string, require('flowcheck/assert').function])); var ret = (function (action, callback) {
        invariant(!this.actions[action], `${action} already exists.`);
        this.actions[action] = callback.bind(this);
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').void);}

    getDispatchToken(): string { var ret = (function () {
        return this.dispatchToken;
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').string);}

    getDispatcher(): Object { var ret = (function () {
        return AppDispatcher;
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').object);}

    emitChange(): void { var ret = (function () {
        super.emit('change');
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').void);}

    addChangeListener(callback: Function): void {require('flowcheck/assert').check(arguments, require('flowcheck/assert').arguments([require('flowcheck/assert').function])); var ret = (function (callback) {
        super.addListener('change', callback);
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').void);}

    removeChangeListener(callback: Function): void {require('flowcheck/assert').check(arguments, require('flowcheck/assert').arguments([require('flowcheck/assert').function])); var ret = (function (callback) {
        super.removeListener('change', callback);
    }).apply(this, arguments); return require('flowcheck/assert').check(ret, require('flowcheck/assert').void);}

}

module.exports = BaseStore;



