/*
 * Application Actions
 * @flow
 */

var AppConstants = require('AppConstants');
var AppDispatcher = require('AppDispatcher');

var {ActionTypes} = AppConstants;

class AppActions {

    dispatch(type: string, data?: Object) {require('flowcheck/assert').check(arguments, require('flowcheck/assert').arguments([require('flowcheck/assert').string, require('flowcheck/assert').optional(require('flowcheck/assert').object)]));
        AppDispatcher.dispatch({ type, ...data });
    }

    initLoad(data?: Object) {require('flowcheck/assert').check(arguments, require('flowcheck/assert').arguments([require('flowcheck/assert').optional(require('flowcheck/assert').object)]));
        this.dispatch(ActionTypes.INIT_LOAD, data);
    }

};

module.exports = new AppActions();



